import { createStyledAttrsMixin, SNA } from '@mekari/pixel-utils';
import { MpGrid } from '@mekari/pixel-grid';

const px = n => typeof n === 'number' ? n + 'px' : n;

const widthToColumns = width => {
  if (Array.isArray(width)) {
    return width.map(widthToColumns);
  }

  if (width !== null && typeof width === 'object' && Object.keys(width).length > 0) {
    const acc = {};

    for (const key in width) {
      acc[key] = `repeat(auto-fit, minmax(${px(width[key])}, 1fr))`;
    }

    return acc;
  }

  if (width != null) {
    return `repeat(auto-fit, minmax(${px(width)}, 1fr))`;
  }

  return null;
};
const countToColumns = count => {
  if (Array.isArray(count)) {
    return count.map(countToColumns);
  }

  if (count !== null && typeof count === 'object' && Object.keys(count).length > 0) {
    const acc = {};

    for (const key in count) {
      acc[key] = `repeat(${count[key]}, 1fr)`;
    }

    return acc;
  }

  if (count != null) {
    return `repeat(${count}, 1fr)`;
  }

  return null;
};

/**
 * Usage :** ` import { MpSimpleGrid } from '@mekari/pixel' `
 **/

const MpSimpleGrid = {
  name: 'MpSimpleGrid',
  mixins: [createStyledAttrsMixin('MpSimpleGrid')],
  props: {
    columns: SNA,
    spacingX: SNA,
    spacingY: SNA,
    spacing: SNA,
    minChildWidth: SNA
  },
  computed: {
    templateColumns() {
      return this.minChildWidth ? widthToColumns(this.minChildWidth) : countToColumns(this.columns);
    }

  },

  render(h) {
    return h(MpGrid, {
      class: this.className,
      props: {
        gap: this.spacing,
        columnGap: this.spacingX,
        rowGap: this.spacingY,
        templateColumns: this.templateColumns
      },
      attrs: this.computedAttrs
    }, this.$slots.default);
  }

};

export { MpSimpleGrid };
