import { MpBox } from '@mekari/pixel-box';
import { MpControlBox } from '@mekari/pixel-control-box';
import { MpVisuallyHidden } from '@mekari/pixel-visually-hidden';
import { createStyledAttrsMixin, useId, cloneVNodeElement } from '@mekari/pixel-utils';

/**
 * Exported radio properties.
 *
 * @prop {string} id - Id of radio.
 * @prop {string} name - Name of radio.
 * @prop {string|number|boolean} value - Value of radio.
 * @prop {boolean} isChecked - If true, radio will be checked.
 * @prop {boolean} defaultIsChecked - If true, radio will be initially checked.
 * @prop {boolean} isReadOnly - If true, radio be read only.
 * @prop {boolean} isDisabled - If true, radio will be disabled.
 * @prop {boolean} isInvalid - If true, radio is marked as invalid.
 * @prop {string} ariaLabel - Optional prop for accessibility.
 * @prop {string} ariaLabelledBy - Optional prop for accessibility.
 */
const useRadioProps = {
  id: [String],
  name: [String],
  value: [String, Number, Boolean],
  isChecked: {
    type: [Boolean],
    default: false
  },
  defaultIsChecked: {
    type: [Boolean],
    default: false
  },
  isReadOnly: {
    type: [Boolean],
    default: false
  },
  isDisabled: {
    type: [Boolean],
    default: false
  },
  isInvalid: {
    type: [Boolean],
    default: false
  },
  ariaLabel: [String],
  ariaLabelledBy: [String]
};

/**
 * Exported radio styles.
 */
const baseStyles = {
  position: 'relative',
  outline: '0px solid transparent',
  borderWidth: '1.5px',
  borderColor: 'inherit',
  borderRadius: 'full',
  userSelect: 'none',
  transition: 'border-color 250ms, outline-color 250ms, background-color 120ms, box-shadow 250ms'
};

const componentStyles = ({
  isChecked,
  isReadOnly,
  isInvalid
}) => {
  const setReadOnlyStyle = styles => {
    return isReadOnly ? isChecked ? styles[0] : styles[1] : styles[2];
  };

  return {
    color: isReadOnly && 'white',
    backgroundColor: setReadOnlyStyle(['blue.400', 'white', 'white']),
    borderColor: setReadOnlyStyle(['blue.400', 'gray.100', 'gray.100']),
    _hover: {
      borderColor: setReadOnlyStyle(['blue.400', 'gray.400', isInvalid ? 'red.400' : 'gray.400'])
    },
    _checked: {
      color: !isReadOnly && 'white',
      backgroundColor: setReadOnlyStyle(['blue.400', 'white', 'blue.400']),
      borderColor: setReadOnlyStyle(['blue.400', 'gray.100', 'blue.400'])
    },
    _disabled: {
      backgroundColor: 'gray.50',
      borderColor: 'gray.100'
    },
    _focus: {
      boxShadow: 'outer'
    },
    _invalid: {
      borderColor: 'red.400'
    },
    _checkedAndHover: {
      backgroundColor: setReadOnlyStyle(['blue.400', 'white', 'blue.500']),
      borderColor: setReadOnlyStyle(['blue.400', 'gray.400', 'blue.500'])
    },
    _checkedAndDisabled: {
      color: 'gray.50',
      backgroundColor: 'gray.400',
      borderColor: 'gray.400'
    }
  };
};

const useRadioStyles = props => {
  return { ...baseStyles,
    ...componentStyles(props)
  };
};

/**
 * A radio typically represents a single option in a mutually exclusive list.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-radio
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-radio
 */

const MpRadio = {
  name: 'MpRadio',
  mixins: [createStyledAttrsMixin('MpRadio')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  model: {
    prop: 'isChecked',
    event: 'checked'
  },
  props: { ...useRadioProps
  },
  computed: {
    getFormControls() {
      if (!this.$useFormControl) {
        return {
          isReadOnly: this.isReadOnly,
          isDisabled: this.isDisabled,
          isInvalid: this.isInvalid
        };
      }

      return this.$useFormControl(this.$props);
    },

    getRadioStyles() {
      return useRadioStyles({
        isChecked: this.isChecked || this.defaultIsChecked,
        isReadOnly: this.getFormControls.isReadOnly,
        isInvalid: this.getFormControls.isInvalid
      });
    },

    getId() {
      return this.id || `radio-${useId(4)}`;
    }

  },
  methods: {
    handleChange(e) {
      if (this.getFormControls.isReadOnly) return false;
      this.$emit('change', e);
      this.$emit('checked', e.target.checked);
    }

  },

  render(h) {
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'label'
      },
      attrs: {
        for: this.getId,
        display: 'inline-flex',
        minHeight: '7',
        verticalAlign: 'top',
        alignItems: 'center',
        cursor: this.getFormControls.isDisabled ? 'not-allowed' : 'pointer',
        ...this.computedAttrs
      }
    }, [h(MpVisuallyHidden, {
      props: {
        as: 'input'
      },
      domProps: {
        value: this.value,
        checked: this.getFormControls.isReadOnly ? this.isChecked : this.defaultIsChecked ? true : this.isChecked
      },
      attrs: {
        type: 'radio',
        id: this.getId,
        name: this.name,
        readOnly: this.getFormControls.isReadOnly,
        disabled: this.getFormControls.isDisabled,
        'aria-readonly': this.getFormControls.isReadOnly,
        'aria-disabled': this.getFormControls.isDisabled,
        'aria-checked': this.getFormControls.isReadOnly ? false : this.isChecked,
        'aria-invalid': this.getFormControls.isInvalid,
        'aria-label': this.ariaLabel,
        'aria-labelledby': this.ariaLabelledBy
      },
      on: {
        change: this.handleChange
      }
    }), h(MpControlBox, {
      attrs: {
        size: '3.5',
        type: 'radio',
        ...this.getRadioStyles
      }
    }, [h(MpBox, {
      props: {
        as: 'span'
      },
      attrs: {
        position: 'absolute',
        width: '7px',
        height: '7px',
        borderRadius: 'full',
        backgroundColor: this.getFormControls.isDisabled ? 'gray.50' : 'currentColor',
        opacity: this.getFormControls.isDisabled ? 1 : 0,
        _readOnly: {
          opacity: 1
        },
        'data-readonly': this.getFormControls.isReadOnly
      }
    })]), this.$slots.default && h(MpBox, {
      props: {
        as: 'span'
      },
      attrs: {
        color: this.getFormControls.isDisabled ? 'gray.400' : 'dark',
        fontSize: 'md',
        fontFamily: 'body',
        lineHeight: 'md',
        minHeight: '7',
        marginLeft: '2',
        paddingTop: '1',
        paddingBottom: '1',
        userSelect: 'none'
      }
    }, this.$slots.default)]);
  }

};

/**
 * Exported radio group properties.
 *
 * @prop {string} name - Name of radio group.
 * @prop {string|number} value - Value of radio group.
 * @prop {string} defaultValue - Initial value of radio group.
 * @prop {boolean} isInline - If true, radios will aligned horizontally.
 * @prop {number|string} spacing - Space between each radio.
 */
const useRadioGroupProps = {
  name: [String],
  value: [String, Number],
  defaultValue: [String],
  isInline: {
    type: [Boolean],
    default: false
  },
  spacing: {
    type: [Number, String],
    default: 2
  }
};

/**
 * A radio group allows user to group a related collection of radios in one visual region.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-radio
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-radio
 */

const MpRadioGroup = {
  name: 'MpRadioGroup',
  mixins: [createStyledAttrsMixin('MpRadioGroup')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: { ...useRadioGroupProps
  },
  computed: {
    getFormControls() {
      if (!this.$useFormControl) {
        return {
          controlId: this.controlId
        };
      }

      return this.$useFormControl(this.$props);
    },

    getValue() {
      return this.value ? this.value : this.defaultValue || '';
    },

    getName() {
      return this.name || `radio-group-${useId()}`;
    }

  },
  methods: {
    handleChange(e) {
      this.$emit('change', e.target.value);
    },

    focus() {
      const rootRef = this.$refs.radioGroup.$el;

      rootRef.focus = () => {
        let inputElement = rootRef.querySelector('input:not(:disabled):checked');
        if (!inputElement) inputElement = rootRef.querySelector('input:not(:disabled)');
        if (inputElement) this.$nextTick(() => inputElement.focus());
      };
    }

  },

  render(h) {
    const controlId = this.getFormControls.controlId;
    const children = this.$slots.default;
    const cloneAllElement = children.map((node, index) => {
      if (!node.tag) return;
      const isLastElement = children.length === index + 1;
      const spacingElement = this.isInline ? {
        marginRight: this.spacing
      } : {
        marginBottom: this.spacing
      };
      const cloneElement = cloneVNodeElement(node, {
        props: {
          name: this.getName,
          isChecked: node.componentOptions.propsData.value === this.getValue
        },
        on: {
          change: this.handleChange
        }
      }, h);
      return h(MpBox, {
        attrs: {
          display: this.isInline ? 'inline-block' : 'block',
          ...(!isLastElement && spacingElement)
        }
      }, [cloneElement]);
    });
    return h(MpBox, {
      class: this.className,
      attrs: {
        role: 'radiogroup',
        id: controlId,
        'aria-label': this.ariaLabel || controlId && `${controlId}-control-label`,
        'aria-labelledby': this.ariaLabelledBy || controlId && `${controlId}-help-text`,
        ...this.computedAttrs
      }
    }, cloneAllElement);
  }

};

export { MpRadio, MpRadioGroup };
