import { createStyledAttrsMixin } from '@mekari/pixel-utils';
import { MpBox } from '@mekari/pixel-box';

/**
 * @name MpTable : @mekari/pixel-table
 * @description Creates a table for your tabular content.
 * @maintainer Dirga Prakesha <dirga.prakesha@mekari.com>
 */
/**
 * Usage : ` import { MpTable, MpTableHead, MpTableBody, MpTableRow, MpTableCell } from '@mekari/pixel' `
 */

const MpTable = {
  name: 'MpTable',
  mixins: [createStyledAttrsMixin('MpTable')],

  provide() {
    return {
      $tableContext: () => this.tableContext
    };
  },

  props: {
    isHoverable: {
      type: [Boolean],
      default: true
    }
  },
  computed: {
    tableContext() {
      return {
        isHoverable: this.isHoverable
      };
    }

  },

  render(h) {
    const styleProps = {
      w: 'full',
      display: 'table',
      borderCollapse: 'collapse',
      borderSpacing: '0px',
      whiteSpace: 'nowrap'
    };
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'table'
      },
      attrs: { ...styleProps,
        ...this.computedAttrs,
        'data-pixel-component': 'MpTable'
      }
    }, this.$slots.default);
  }

};
const MpTableContainer = {
  name: 'MpTableContainer',
  mixins: [createStyledAttrsMixin('MpTableContainer')],

  render(h) {
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'div'
      },
      attrs: { ...this.computedAttrs,
        'data-pixel-component': 'MpTableContainer'
      }
    }, this.$slots.default);
  }

};
const MpTableHead = {
  name: 'MpTableHead',
  mixins: [createStyledAttrsMixin('MpTableHead')],
  props: {
    isFixed: [Boolean]
  },

  render(h) {
    const styleProps = {
      w: 'full'
    };
    const fixedStyleProps = {
      position: 'sticky',
      top: '0',
      backgroundColor: 'white',
      boxShadow: '0px 2px #D0D6DD',
      zIndex: 'sticky'
    };
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'thead'
      },
      attrs: { ...styleProps,
        ...(this.isFixed && { ...fixedStyleProps
        } || {}),
        ...this.computedAttrs,
        'data-pixel-component': 'MpTableHead'
      }
    }, this.$slots.default);
  }

};
const MpTableBody = {
  name: 'MpTableBody',
  mixins: [createStyledAttrsMixin('MpTableBody')],

  render(h) {
    const styleProps = {
      w: 'full'
    };
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'tbody'
      },
      attrs: { ...styleProps,
        ...this.computedAttrs,
        'data-pixel-component': 'MpTableBody'
      }
    }, this.$slots.default);
  }

};
const MpTableRow = {
  name: 'MpTableRow',
  mixins: [createStyledAttrsMixin('MpTableRow')],

  render(h) {
    const styleProps = {
      w: 'full',
      boxSizing: 'border-box',
      whiteSpace: 'nowrap'
    };
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'tr'
      },
      attrs: { ...styleProps,
        ...this.computedAttrs,
        role: 'group',
        'data-pixel-component': 'MpTableRow'
      }
    }, this.$slots.default);
  }

};
const MpTableCell = {
  name: 'MpTableCell',
  mixins: [createStyledAttrsMixin('MpTableCell')],
  inject: ['$tableContext'],
  props: {
    as: [String],
    isFixed: [Boolean]
  },
  computed: {
    context() {
      return this.$tableContext();
    }

  },

  render(h) {
    const {
      isHoverable
    } = this.context;
    const styleProps = {
      pl: '2',
      pr: '4',
      py: '2',
      h: '12',
      fontSize: 'md',
      fontFamily: 'body',
      fontWeight: this.as === 'th' ? 'semibold' : 'regular',
      lineHeight: 'md',
      letterSpacing: 'normal',
      textAlign: 'left',
      borderBottom: '1px',
      borderColor: 'gray.100',
      transition: 'all 120ms',
      ...(isHoverable && {
        _groupHover: {
          cursor: this.as === 'td' ? 'pointer' : 'inherit',
          backgroundColor: this.as === 'td' ? 'ice.50' : 'white'
        }
      })
    };
    const fixedStyleProps = {
      position: 'sticky',
      backgroundColor: 'white',
      zIndex: 'docked'
    };
    return h(MpBox, {
      class: this.className,
      props: {
        as: this.as
      },
      attrs: { ...styleProps,
        ...(this.isFixed && { ...fixedStyleProps
        } || {}),
        ...this.computedAttrs,
        'data-pixel-component': 'MpTableCell'
      }
    }, this.$slots.default);
  }

};

export { MpTable, MpTableBody, MpTableCell, MpTableContainer, MpTableHead, MpTableRow };
