import { MpBox } from '@mekari/pixel-box';
import { MpControlBox } from '@mekari/pixel-control-box';
import { MpIcon } from '@mekari/pixel-icon';
import { MpVisuallyHidden } from '@mekari/pixel-visually-hidden';
import { createStyledAttrsMixin, useId } from '@mekari/pixel-utils';

/**
 * Exported segmented control properties.
 *
 * @prop {string} name - Name of segmented control.
 * @prop {array} data - Data of segmented control.
 * @prop {string|number} value - Value of segmented control.
 * @prop {string} defaultValue - Initial value of segmented control.
 * @prop {boolean} isFullWidth - If true, segmented control will span full width of it parent.
 * @prop {boolean} isDisabled - If true, segmented control will be disabled.
 */
const useSegmentedControlProps = {
  name: [String],
  data: [Array],
  value: [String, Number],
  defaultValue: [String],
  isFullWidth: {
    type: [Boolean],
    default: false
  },
  isDisabled: {
    type: [Boolean],
    default: false
  }
};

/**
 * Exported segmented control styles.
 */
const baseStyles = {
  display: 'flex',
  alignItems: 'center',
  height: '7',
  paddingY: '1',
  userSelect: 'none',
  borderRadius: 'sm',
  transition: 'all 250ms'
};

const componentStyles = () => {
  return {
    color: 'gray.600',
    backgroundColor: 'white',
    _hover: {
      color: 'blue.700'
    },
    _checked: {
      color: 'blue.400',
      backgroundColor: 'blue.50'
    },
    _disabled: {
      color: 'gray.400'
    },
    _checkedAndHover: {
      color: 'blue.700'
    },
    _checkedAndFocus: {
      color: 'blue.700',
      boxShadow: 'outer'
    },
    _checkedAndDisabled: {
      color: 'gray.400',
      backgroundColor: 'gray.50'
    }
  };
};

const useSegmentedControlStyles = props => {
  return { ...baseStyles,
    ...componentStyles()
  };
};

/**
 * Horizontal control made of multiple segments alternative to radio group component.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-segmented-control
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-segmented-control
 */

const MpSegmentedControl = {
  name: 'MpSegmentedControl',
  mixins: [createStyledAttrsMixin('MpSegmentedControl')],
  model: {
    prop: 'value',
    event: 'change'
  },
  props: { ...useSegmentedControlProps
  },
  computed: {
    getSegmentedControlStyles() {
      return useSegmentedControlStyles({
        isFullWidth: this.isFullWidth
      });
    },

    getValue() {
      return this.value ? this.value : this.defaultValue || '';
    },

    getName() {
      return this.name || `segmented-control-${useId()}`;
    }

  },
  methods: {
    handleChange(e) {
      this.$emit('change', e.target.value);
    }

  },

  render(h) {
    return h(MpBox, {
      class: this.className,
      attrs: {
        role: 'radiogroup',
        position: 'relative',
        display: 'inline-flex',
        gap: '1',
        flexDirection: 'row',
        justifyContent: this.isFullWidth ? 'space-around' : 'flex-start',
        alignItems: 'center',
        width: this.isFullWidth ? '100%' : 'auto',
        height: '9.5',
        borderWidth: '1px',
        borderColor: 'gray.100',
        borderRadius: 'md',
        outline: '0px solid transparent',
        padding: '1',
        ...this.computedAttrs
      }
    }, [this.data.map(item => h(MpBox, {
      props: {
        as: 'label'
      },
      attrs: {
        flex: this.isFullWidth ? '1 1 0%' : '1 1 auto',
        cursor: this.isDisabled || item.isDisabled ? 'not-allowed' : 'pointer'
      }
    }, [h(MpVisuallyHidden, {
      props: {
        as: 'input'
      },
      domProps: {
        value: item.value || item,
        checked: item.value === this.getValue
      },
      attrs: {
        type: 'radio',
        id: item.id || `segmented-control-${useId(4)}`,
        name: this.getName,
        disabled: this.isDisabled || item.isDisabled,
        'aria-disabled': this.isDisabled || item.isDisabled,
        'aria-checked': item.value === this.getValue,
        'aria-label': item.ariaLabel,
        'aria-labelledby': item.ariaLabelledBy
      },
      on: {
        change: this.handleChange
      }
    }), h(MpControlBox, {
      attrs: {
        type: 'radio',
        gap: item.icon && item.label ? '2' : '0',
        paddingX: item.icon ? item.label ? '2' : '1' : '2',
        ...this.getSegmentedControlStyles
      }
    }, [item.icon && h(MpIcon, {
      props: {
        name: item.icon,
        size: 'sm'
      },
      attrs: {
        color: 'current',
        opacity: '1',
        focusable: false
      }
    }), h(MpBox, {
      props: {
        as: 'span'
      },
      attrs: {
        color: 'current',
        fontSize: 'md',
        fontFamily: 'body',
        lineHeight: 'md',
        opacity: '1'
      }
    }, item.label || item)])]))]);
  }

};

export { MpSegmentedControl };
