import { MpBox } from '@mekari/pixel-box';
import { MpControlBox } from '@mekari/pixel-control-box';
import { MpVisuallyHidden } from '@mekari/pixel-visually-hidden';
import { createStyledAttrsMixin, useId } from '@mekari/pixel-utils';

/**
 * Exported toggle properties.
 *
 * @prop {string} id - Id of toggle.
 * @prop {string} name - Name of toggle.
 * @prop {string|boolean} value - Value of toggle.
 * @prop {boolean} isChecked - If true, toggle will be checked.
 * @prop {boolean} defaultIsChecked - If true, toggle will be initially checked.
 * @prop {boolean} isReadOnly - If true, toggle be read only.
 * @prop {boolean} isDisabled - If true, toggle will be disabled.
 * @prop {string} ariaLabel - Optional prop for accessibility.
 * @prop {string} ariaLabelledBy - Optional prop for accessibility.
 */
const useToggleProps = {
  id: [String],
  name: [String],
  value: [String, Boolean],
  isChecked: {
    type: [Boolean],
    default: false
  },
  defaultIsChecked: {
    type: [Boolean],
    default: false
  },
  isReadOnly: {
    type: [Boolean],
    default: false
  },
  isDisabled: {
    type: [Boolean],
    default: false
  },
  ariaLabel: [String],
  ariaLabelledBy: [String]
};

/**
 * Exported toggle styles.
 */
const baseStyles = {
  width: '7',
  height: '4',
  backgroundColor: 'gray.100',
  outline: '0px solid transparent',
  borderWidth: '1px',
  borderColor: 'gray.100',
  borderRadius: 'full',
  justifyContent: 'flex-start',
  padding: '0.5'
};

const componentStyles = ({
  theme,
  isChecked,
  isReadOnly,
  isDisabled
}) => {
  const setReadOnlyStyle = styles => {
    return isReadOnly ? isChecked ? styles[0] : styles[1] : styles[2];
  };

  return {
    color: isReadOnly && 'white',
    backgroundColor: setReadOnlyStyle(['blue.400', 'gray.100', 'gray.100']),
    borderColor: setReadOnlyStyle(['blue.400', 'gray.100', 'gray.100']),
    _hover: {
      backgroundColor: setReadOnlyStyle(['blue.500', 'gray.400', 'gray.400']),
      borderColor: setReadOnlyStyle(['blue.500', 'gray.400', 'gray.400'])
    },
    _checked: {
      color: !isReadOnly && 'white',
      backgroundColor: setReadOnlyStyle(['blue.400', 'gray.100', 'blue.400']),
      borderColor: setReadOnlyStyle(['blue.400', 'gray.100', 'blue.400'])
    },
    _disabled: {
      backgroundColor: 'gray.50',
      borderColor: 'gray.100'
    },
    _focus: {
      boxShadow: 'outer',
      backgroundColor: setReadOnlyStyle(['blue.400', 'blue.100', 'blue.100']),
      borderColor: setReadOnlyStyle(['blue.500', 'blue.400', 'blue.400'])
    },
    _child: {
      backgroundColor: isDisabled ? 'gray.100' : 'white',
      transform: isReadOnly ? isChecked ? `translateX(calc(${theme.sizes[7]} - ${theme.sizes[4]}))` : 'translateX(0)' : 'translateX(0)'
    },
    _checkedAndFocus: {
      boxShadow: 'outer',
      backgroundColor: setReadOnlyStyle(['blue.400', 'gray.100', 'blue.400']),
      borderColor: setReadOnlyStyle(['blue.500', 'blue.400', 'blue.500'])
    },
    _checkedAndHover: {
      backgroundColor: setReadOnlyStyle(['blue.500', 'gray.400', 'blue.500']),
      borderColor: setReadOnlyStyle(['blue.500', 'gray.400', 'blue.500'])
    },
    _checkedAndDisabled: {
      backgroundColor: 'gray.400',
      borderColor: 'gray.400'
    },
    _checkedAndChild: {
      backgroundColor: isDisabled ? 'gray.50' : 'white',
      transform: isReadOnly ? isChecked ? `translateX(calc(${theme.sizes[7]} - ${theme.sizes[4]}))` : 'translateX(0)' : `translateX(calc(${theme.sizes[7]} - ${theme.sizes[4]}))`
    }
  };
};

const useToggleStyles = props => {
  return { ...baseStyles,
    ...componentStyles(props)
  };
};

/**
 * A toggle is simply an alternate appearance for a checkbox that simulates on/off instead of checked/unchecked.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-toggle
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-toggle
 */

const MpToggle = {
  name: 'MpToggle',
  mixins: [createStyledAttrsMixin('MpToggle')],
  model: {
    prop: 'isChecked',
    event: 'change'
  },
  props: { ...useToggleProps
  },
  computed: {
    getToggleStyles() {
      return useToggleStyles({
        isChecked: this.isChecked || this.defaultIsChecked,
        isReadOnly: this.isReadOnly,
        isDisabled: this.isDisabled,
        theme: this.theme
      });
    },

    getId() {
      return this.id || `toggle-${useId(4)}`;
    }

  },
  methods: {
    handleChange(e) {
      if (this.isReadOnly) return;

      if (this.$listeners.change && typeof this.$listeners.change === 'function') {
        this.$listeners.change(!this.isChecked, e);
      } else if (Array.isArray(this.$listeners.change)) {
        this.$listeners.change.forEach(handler => typeof handler === 'function' && handler(!this.isChecked, e));
      }
    }

  },

  render(h) {
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'label'
      },
      attrs: {
        for: this.getId,
        display: 'inline-flex',
        minHeight: '7',
        verticalAlign: 'top',
        alignItems: 'center',
        cursor: this.isDisabled ? 'not-allowed' : 'pointer',
        ...this.computedAttrs
      }
    }, [h(MpVisuallyHidden, {
      props: {
        as: 'input'
      },
      domProps: {
        value: this.value,
        defaultChecked: this.isReadOnly ? undefined : this.defaultIsChecked,
        checked: this.isReadOnly ? this.isChecked : this.defaultIsChecked ? undefined : this.isChecked
      },
      attrs: {
        type: 'checkbox',
        id: this.getId,
        name: this.name,
        readOnly: this.isReadOnly,
        disabled: this.isDisabled,
        'aria-readonly': this.isReadOnly,
        'aria-disabled': this.isDisabled,
        'aria-checked': this.isReadOnly ? false : this.isChecked,
        'aria-label': this.ariaLabel,
        'aria-labelledby': this.ariaLabelledBy
      },
      on: {
        change: this.handleChange
      }
    }), h(MpControlBox, {
      attrs: { ...this.getToggleStyles
      }
    }, [h(MpBox, {
      attrs: {
        height: '2.5',
        width: '2.5',
        borderRadius: 'full',
        transition: 'transform 250ms'
      }
    })]), this.$slots.default && h(MpBox, {
      attrs: {
        color: this.isDisabled ? 'gray.400' : 'dark',
        fontSize: 'md',
        fontFamily: 'body',
        lineHeight: 'md',
        minHeight: '7',
        marginLeft: '3',
        paddingTop: '1',
        paddingBottom: '1',
        userSelect: 'none'
      }
    }, this.$slots.default)]);
  }

};

export { MpToggle };
